body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  display: -ms-flexbox;
  display: flex;
    width: 100%;
    min-height: 100vh;
    height: 100%;
}

.sidebar {
  position: fixed;
  z-index: 1;
  background-color: #1b1c1d;
  width: 250px;
  max-height: 100vh;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  top: 0;
  bottom: 0;
}

.sidebar a {
  text-decoration:  none;
}

.sidebar p{
  padding: 0 15px;
  color: #7f7f7f;
  font-size: 1rem;
}

.sidebar .bottom {
  position: absolute;
  bottom: 10px;
  padding: 15px;
}

.sidebar .bottom span {
  color: #7f7f7f;
  font-weight: bolder;
  margin-left: 5px;
}

.sidebar .bottom svg {
  height: 16px;
}

.sidebar-title {
  color: #ffffff;
  margin: 15px;
}

.sidebar-title em {
  font-size: 0.7rem;
  color: #7f7f7f;
  margin-left: 10px;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  padding: 2rem;
  width: 100%;
}



